<template>
    <div class="extensionsgl extensionsgl--custom">
        <div class="btns">
          <el-button class="btn" @click="exportCdrs">导出</el-button>
            <div class="search float-r">
             <el-input class="search-input" placeholder="号码/租户号"  :maxlength="255" icon="search" v-model="searchInput" :on-icon-click="handleCdrsSearch">
             </el-input>
            </div>
            <div class="search-div">
              <el-date-picker v-model="searchInputTime" type="datetimerange" align="right" placeholder="选择通话时间范围" :picker-options="pickerOptions2" style="width:240px"> </el-date-picker>
               通话时长：<el-input class='recodinput'  style="width:80px" v-model="second1"></el-input>-<el-input class='recodinput' style="width:80px" v-model="second2" ></el-input>秒
             </div>
        </div>
        <div class="tables">
        <el-table class="table" ref="multipleTable" :data="cdrsData" border tooltip-effect="dark" @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column prop="vlanId" label="租户号"></el-table-column>
            <el-table-column prop="uuid" label="UUID">
                <template slot-scope="scope">
                    <el-button @click="handlecdrsCheck(scope.$index, scope.row)" type="text">{{scope.row.uuid}}</el-button>
                </template>
            </el-table-column>
            <el-table-column prop="callId" label="CallID"></el-table-column>
            <el-table-column prop="callerIdNumber" label="主叫号码"></el-table-column>
            <el-table-column prop="destinationNumber" label="被叫号码"></el-table-column>
            <el-table-column prop="startStamp" label="开始时间"  :formatter="dataFormat"></el-table-column>
            <el-table-column prop="answerStamp" label="应答时间"  :formatter="dataFormat"></el-table-column>
            <el-table-column prop="endStamp" label="结束时间"  :formatter="dataFormat"></el-table-column>
            <el-table-column prop="duration" label="通话时长"></el-table-column>
            <el-table-column prop="billsec" label="计费时长"></el-table-column>
            <el-table-column prop="hangupCause" label="挂断原因"></el-table-column>
            <!-- <el-table-column prop="direction" label="呼叫方向"></el-table-column> -->
            <el-table-column prop="recordPath" label="录音文件">
                <template slot-scope="scope">
                    <el-button @click="download(scope.$index, scope.row)" type="text">{{scope.row.recordTitle}}</el-button>
                </template>
            </el-table-column>
        </el-table>
        <!--查看弹窗-->
        <el-dialog title="查看话务详单" class="w-500" :visible.sync="dialogFormCheck" :close-on-click-modal='false'>
            <div class="item">UUID: {{showCheck.uuid}}</div>
            <div class="item">租户号: {{showCheck.vlanId}}</div>
            <div class="item">主叫号码: {{showCheck.callerIdNumber}}</div>
            <div class="item">被叫号码: {{showCheck.destinationNumber}}</div>
            <div class="item">开始时间: {{showCheck.startStamp}}</div>
            <div class="item">应答时间: {{showCheck.answerStamp}}</div>
            <div class="item">结束时间: {{showCheck.endStamp}}</div>
            <div class="item">通话时长: {{showCheck.duration}}</div>
            <div class="item">挂断原因: {{showCheck.hangupCause}}</div>
            <div class="item">呼叫方向: {{showCheck.direction}}</div>
            <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormCheck = false">取 消</el-button>
            <el-button type="primary" @click="dialogFormCheck=false">确 定</el-button>
            </div>
        </el-dialog>
        <el-dialog title="请选择租户" class="w-500" :visible.sync="dialogInputVlanId" :close-on-click-modal='false'>
          <el-form :model="inputVlanId" :rules="formRules" labelPosition="left" ref="inputVlanId">
            <el-form-item prop="inputVlanId" label="租户名称" :label-width="formLabelWidth">
              <el-select v-model.number="inputVlanId.vlanId" placeholder="请选择" >
                  <el-option  v-for="item in allCompanyId" :key="item.value" :label="item.label" :value="item.value" >
                  </el-option>
              </el-select> 
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialogInputVlanId = false">取 消</el-button>
            <el-button type="primary" @click="exportCdrsFileByVlanId()">确 定</el-button>
          </div>
        </el-dialog>
        </div>
        <div class="float-r Pagination" style="text-align: left;margin-top: 10px;">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-size="10" layout="total, prev, pager, next" :total="count">
        </el-pagination>
        </div>
    </div>
</template>

<script>

  import {
    cdrsList,
    savecdrs,
    deletecdrsByIds,
    exportCdrsFile,
  }
    from '@/api/getData';
  import ElTableTreeColumn from '../base/table-tree/table-column.js';
  import moment from 'moment';
  
  export default {
    data() {
      var checkNumber = (rule, value, callback) => { //必须填而且必须是数字
            if (value == undefined || "" === value) {
                callback(new Error('此处不能为空'));
            }
            if (!Number.isInteger(value)) {
                callback(new Error('请输入数字值'));
            } else {
                callback();
            }
        };
        var checkNull = (rule, value, callback) => { //要么不填，要填必须是数字
            if (value == undefined || "" === value) {
                callback();
            }
            if (!Number.isInteger(value)) {
                callback(new Error('请输入数字值'));
            } else {
                callback();
            }
      };
      return {
        //分页
        currentRow: null,
        limit: 10,
        count: 0,
        currentPage: 1,
        searchInput: '', //搜索框
        searchInputTime:'',
        second1:'',
        second2:'',
        dialogInputVlanId:false,
        cdrsData: [],
        showCheck: {}, //查看对象
        dialogFormCheck: false, //查看弹窗
        exportCdrsFile:exportCdrsFile,
        multipleSelection: [],
        formLabelWidth: '80px',
        formRules: {
           duration: [{validator: checkNull, trigger: 'blur'}],
           billsec: [{validator: checkNull, trigger: 'blur'}],
         
        },
        inputVlanId:{
          vlanId:'',
        },
         pickerOptions2: {
          shortcuts: [{
            text: '最近一周',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近三个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }]
        },
      }
    },
    computed: {
        allCompanyId:function() {
             return this.$store.baseStore.getters.getAllCompanyInfo;//allCompanyId依赖于getAllCompanyInfo，getAllCompanyInfo变了后allCompanyId会重新计算
        }
    },
    mounted(){
        var coms = this.$store.baseStore.getters.getAllCompanyInfo;
        if(coms == undefined || coms.length == 0){
            this.$store.baseStore.dispatch('getAllDeptId');
        }else{
            this.$data.allCompanyId= coms;
        }
        
    },
    created() {
      this.getcdrsList(); //加载数据
    },
    components: {
      ElTableTreeColumn
    },
    methods: {
      //获取分页 总条数
      async getcdrsList() {
        try {
          let query = {
            number: this.searchInput,
            dateTime1: this.dateFormat(this.searchInputTime[0]),
            dateTime2: this.dateFormat(this.searchInputTime[1]),
            second1:this.second1,
            second2:this.second2,
          }
          //分页
          const res = await cdrsList({
            pageindex:this.currentPage,
            pagesize:this.limit,
            query:JSON.stringify(query)
          });
          if (res.resultCode == '0') {
            this.cdrsData = [];
            //防止后台数据幅值，，前台页面只需要改动此处就好，还有一个好处 就是只选择我需要的数据
            this.showCheck = res.result.data;
            this.count=res.result.total;
            res.result.data.forEach(item => {
              var hangupCause;
              if(item.hangupCause == 'NO_ROUTE_DESTINATION'){
                hangupCause = '无路由';
              } else if(item.hangupCause = 'NORMAL_CLEARING'){
                hangupCause = '正常释放';
              } else if(item.hangupCause = 'NO_USER_RESPONSE'){
                hangupCause = '用户无应答';
              } else if(item.hangupCause = 'CALL_REJECTED'){
                hangupCause = '被叫拒绝';
              } else {
                hangupCause = item.hangupCause;
              }
              var recordTitle = '点击下载';
              if(item.recordPath == null){
                recordTitle = '文件缺失';
              }
              const tableItem = {
                recordPath:item.recordPath,
                recordTitle:recordTitle,
                answerStamp:item.answerStamp,
                billsec:item.billsec,
                callId:item.callId,
                callerIdNumber:item.callerIdNumber,
                context:item.context,
                destinationNumber:item.destinationNumber,
                direction:item.direction,
                duration:item.duration,
                endStamp:item.endStamp,
                hangupCause:hangupCause,
                id:item.id,
                localIpV4:item.localIpV4,
                readCodec:item.readCodec,
                route:item.route,
                routeIp:item.routeIp,
                startStamp:item.startStamp,
                uuid:item.uuid,
                writeCodec:item.writeCodec,
                vlanId:item.vlanId,
              }
              this.cdrsData.push(tableItem)
            })
          } else {
            throw new Error(res.message)
          }
        } catch (err) {
          console.log('获取数据失败', err);
        }
      },
      exportCdrs(){//导出话单
        if(this.$store.baseStore.getters.getUserInfo.managerLevel == '999'){
          this.$data.inputVlanId.vlanId = this.$store.baseStore.getters.getUserInfo.vlanId;
          this.$data.dialogInputVlanId = true;
        } else {//租户管理员和分机用户后端获取租户号和分机号
          window.location.href = this.$data.exportCdrsFile+'?number='+this.searchInput+"&dateTime1="+this.dateFormat(this.searchInputTime[0])+
          "&dateTime2="+this.dateFormat(this.searchInputTime[1])+"&second1="+this.second1+"&second2="+this.second2;
        }
      },
      exportCdrsFileByVlanId(){
        window.location.href = this.$data.exportCdrsFile+'?vlanId='+this.$data.inputVlanId.vlanId+"&number="+this.searchInput+"&dateTime1="+
        this.dateFormat(this.searchInputTime[0])+"&dateTime2="+this.dateFormat(this.searchInputTime[1])+"&second1="+this.second1+"&second2="+this.second2;
        this.$data.dialogInputVlanId = false;
      },
      handleSizeChange(val) {
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        this.getcdrsList();
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      handleCdrsSearch() {
        this.getcdrsList();
      },
      formatter(row, column) {
        return ' -- ' + row.name;
      },
      dataFormat(row, column) {
         var time =  row[column.property];
         if(time != undefined && time != ''){
            return this.$moment(time).format("YYYY-MM-DD HH:mm:ss")  
         }else{
            return '';
         }
         
      },
      dateFormat(time) {
         if(time != undefined && time != ''){
            return this.$moment(time).format("YYYY-MM-DD HH:mm:ss")  
         }else{
            return '';
         }
      },
       //下载录音
      download(index, row) {
        var path = row.recordPath;
        if(path != null){
           if(path.startsWith('http')){
               window.open(path, '_blank');
           }else{
               window.open("/ucc/virtual/download?type=fullpath&path=" + path, '_blank');
           }
           
        }
      },
      //查看
      handlecdrsCheck(index, row) {
        this.dialogFormCheck = true;
        row.startStamp = this.dateFormat(row.startStamp);
        row.endStamp = this.dateFormat(row.endStamp);
        row.answerStamp = this.dateFormat(row.answerStamp);
        this.$data.showCheck = row;
      },
    }
  }

</script>
<style scope lang="less">
  .extensionsgl {
    padding: 33px 0 0 26px;
    box-sizing: border-box;
    .float-l {
      float: left;
    }
    .float-r {
      float: right;
    }
    //table 改组建
    .el-table {
      width: 100%;
      height: 100%;
      border: 0;
      &:before,
      &:after {
        display: none;
      }
      .el-table__body-wrapper {
        overflow: hidden;
      }
      th,
      td,
      .cell {
        border: 0px;
        background-color: initial;
        padding: 0px;
      }
      .cell {
        padding-left: 10px;
      }
      tr {
        border-bottom: 1px solid #e6e6e6;
      }
      thead tr {
        border-bottom: 2px solid #e6e6e6;
      }
    }
    .w-500 {
      .el-dialog {
        width: 500px;
        .el-dialog__body {
          max-height: 500px;
          overflow-y: auto;
          .item{
            height:40px;
            line-height:40px;
          }
        }
      }
    }
    .w-1000 {
      .el-dialog {
        width: 1000px;
        .el-dialog__body {
          max-height: 500px;
          overflow-y: auto;
        }
      }
    }
    .el-button--text {
      color: #00a8d7;
    }
    .btns {
      .btn {
        width: 80px;
        height: 36px;
        border: 1px solid #e6e6e6;
        color: #454545;
      }
      .float-r {
        float: right;
      }
    }
    .tables {
      .table {
        border: 0;
        margin: 15px 0;
      }
    }
    .m-t-26 {
      margin-top: 26px;
    }
    .extensionsgl-type {
      margin-right: 20px;
    }
    .nav {
      color: #99a9bf;
      cursor: pointer;
      transition: .3s;
      vertical-align: baseline;
      margin-right: 30px;
      &:hover {
        color: #48576a;
      }
    }
    .tree-grid {
      margin: 15px 0;
    }
    .recodinput{
      width: 50px;
    }
    .search-div{
      float: right;
      width: 550px;
    }
  }
</style>
<style lang="less">
    .extensionsgl.extensionsgl--custom .el-table .el-table__body-wrapper {
        overflow: auto;
    }
</style>


